/* Estilos generales */
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

* {
  box-sizing: border-box; }

body {
  max-width: 100vw; }

html {
  scroll-behavior: smooth; }

.code {
  font-size: 16px;
  background: #d8d8d8;
  padding: 0 5px;
  border-radius: 5px; }

.link__adalab:hover {
  background-color: #099d8d;
  color: #fff; }

.link__github:hover {
  background-color: #CCC;
  color: #000; }

.header {
  position: fixed;
  width: 100vw;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center; }

.header .header-name {
  height: 60px;
  width: 100%;
  background-color: var(--color-bg);
  color: var(--color-w);
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: var(--font-t); }

.header .menu {
  height: 45px;
  background-color: var(--color-ag);
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  text-transform: uppercase; }

.menu .header-link {
  text-decoration: none;
  color: var(--color-w);
  font-family: var(--font-t); }

@media (min-width: 768px) {
  /* ------Header-------- */
  .header {
    position: fixed;
    z-index: 2;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100vw;
    background-color: var(--color-bg);
    height: 60px; }
  .header .header-name {
    padding-left: 45px;
    text-align: left;
    display: flex;
    justify-content: left; }
  .header .menu {
    background-color: var(--color-bg);
    height: 60px;
    display: flex;
    justify-content: space-around;
    padding: right 26px; }
  .menu .header-link {
    padding-right: 19px; } }

#container {
  box-sizing: content-box;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: 100%;
  height: 200px;
  margin: 0 auto; }
  #container__imagebox {
    box-sizing: border-box;
    border: solid 1.5px black;
    height: 320px;
    margin-bottom: 60px; }
  #container__slogan {
    width: 400px;
    height: 100px;
    background-color: #ffffff;
    border: solid 1px #000000;
    font-family: "Open Sans", sans-serif;
    position: absolute;
    margin: 14px;
    transform: translateY(140px);
    text-align: center;
    padding: 30px;
    /*cambiar con texto definitivo*/
    font-weight: bold;
    font-size: 18px; }
  @media all and (min-width: 768px) {
    #container {
      transform: translateY(50px);
      background: #5a4545; } }

#adalabers {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 20px; }

.team__title {
  box-sizing: border-box;
  font-family: "Rubik", sans-serif;
  text-transform: uppercase;
  color: #14d9c4;
  text-align: center;
  font-size: 1.5rem; }

.team__text {
  box-sizing: border-box;
  border: solid 1px black;
  background-color: #ffffff;
  text-align: center;
  font-family: "Open Sans", sans-serif;
  margin: 15px;
  margin-bottom: 20px;
  padding: 20px; }
  @media all and (min-width: 768px) {
    .team__text {
      width: 678px;
      margin: 45px;
      height: 168px; } }
  @media all and (min-width: 1000px) {
    .team__text {
      width: 678px;
      margin: 45px;
      height: 200px; } }

.strenghts-container {
  width: 100%;
  box-sizing: border-box;
  font-family: var(--font-p);
  color: var(--color-b);
  background-color: #f1f1f1;
  line-height: 1.4rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  text-align: center;
  position: relative; }

.strenghts-container .triangle {
  display: flex;
  justify-content: center;
  align-self: center;
  border-left: 30px solid transparent;
  border-right: 30px solid transparent;
  border-top: 30px solid white; }

.strenghts-container .strenghts-wrapper {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px; }

.strenghts-container .strenghts-font-style {
  font-family: var(--font-t);
  color: var(--color-bg);
  line-height: 1.4rem;
  font-weight: bold;
  font-size: 22px;
  padding: 0.625rem; }

.strenghts-container .weaknesses-wrapper {
  box-sizing: border-box;
  display: flex;
  flex-direction: column; }

@media (min-width: 768px) {
  .strenghts-container {
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    padding: 1rem; }
  .triangle {
    position: absolute;
    top: 0; }
  .strenghts-wrapper {
    width: 45%;
    margin: 1.2rem; }
  .weaknesses-wrapper {
    width: 45%;
    margin: 1.2rem; } }

a {
  color: #14d9c4; }

.aboutus {
  text-align: center;
  padding-top: 30px; }

.title {
  font-family: var(--font-t);
  color: var(--color-bg);
  font-weight: bold;
  font-size: 22px;
  padding-bottom: 10px; }

.name {
  font-family: var(--font-p);
  font-size: 20px;
  line-height: 2.25;
  letter-spacing: 1px;
  text-align: center;
  color: var(--black);
  font-weight: bold; }

.icons {
  padding-top: 15px; }

.bio {
  font-family: var(--font-p);
  font-size: 16px;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: center;
  color: #54585a; }

.profile {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  text-align: center;
  font-family: var(--font-p);
  position: relative; }

.footer {
  border-top: solid 1px;
  border-color: var(--color-dg);
  margin-top: 45px; }

.footer-container {
  margin-top: 30px;
  text-align: center; }

.footer-container .footer-copy {
  margin: 20px auto;
  color: var(--color-sg);
  font-family: var(--font-p); }

.footer-container .footer-list {
  text-transform: uppercase;
  margin-top: 20px auto;
  list-style: none; }

.footer-list .link {
  text-decoration: none;
  color: var(--color-dg);
  font-family: var(--font-p);
  line-height: 1.43;
  font-weight: bold; }

.footer-container .footer-img {
  min-width: 115.9px;
  min-height: 45px;
  margin-top: 30px; }

@media (min-width: 768px) {
  .footer {
    width: 100vw;
    margin-top: 0px; }
  .footer-container {
    width: 100vw;
    display: flex;
    align-items: center;
    flex-direction: row; }
  .footer-container .footer-copy {
    margin: 0px;
    display: flex;
    justify-content: flex-start; }
  .footer-container .footer-list {
    display: flex;
    justify-content: flex-end;
    position: relative;
    top: 25px; }
  .footer-list .link {
    display: flex; }
  .footer-container .footer-img {
    display: flex;
    justify-content: flex-start;
    width: 115.9px;
    height: 45px; } }
